body {
 margin: 0;
 padding: 0;

 color: white;
}

header::before {
 background: url("https://source.unsplash.com/random") no-repeat center
  center/cover;
 content: "";
 position: absolute;
 top: 0;
 left: 0;
 width: 100%;
 height: 100%;
 z-index: -1;
 opacity: 0.6;
}

.navigation {
 font-family: "Bree Serif", serif;
 font-size: 20px;
 display: flex;
}

li {
 list-style: none;
 padding: 20px 23px;
}

section {
 height: 344px;
 font-family: "Bree Serif", serif;
 margin: 3px 230px;
 display: flex;
 flex-direction: column;
 align-items: center;
 justify-content: center;
}

h1 {
 font-size: 4rem;
}

p {
 text-align: center;
}

@keyframes animate {
 0%,
 10%,
 100% {
  width: 0;
 }
 70%,
 90% {
  width: 100%;
 }
}

main {
 position: absolute;
 top: 10%;
 left: 20%;
 font-size: 20px;
 transform: translate(-50%, -50%);
 text-transform: uppercase;
 font-family: consolas;
 letter-spacing: 5px;
 margin: 0;
 padding: 0;
 color: transparent;
}

main:before {
 content: attr(data-text);
 position: absolute;
 top: 0;
 left: 0;
 width: 100%;
 height: 100%;
 color: #000;
 overflow: hidden;
 border-right: 1px solid #000;
 animation: type 8s steps(20) infinite;
 white-space: nowrap;
}

nav{
    padding: 10px;
}
 li{
    padding: 10px;
    margin: 10px;
    position: absolute;
    left: 1px;
    font-size: 20px;
    
}


@keyframes type {
    0%{
        width: 0;
    }
    50%{
        width: 100%;
    }
    0%{
        width: 0;
    }
}



@media (max-width: 768px) {
    li{
        font-size: 15px;
    }

    main{
        position: absolute;
        top: 10%;
        left: 40%;
        font-size: 10px;
        word-wrap: break-word;
    }
} 